import React, { useContext, useEffect, useState } from 'react';
import { SocketContext } from '../../context/socket';

import logo_symbol from '../../assets/logo_symbol.png'

export default function Dashboard() {
  const socket = useContext(SocketContext);
  const [location, setLocation] = useState(null)
  const [room, setRoom] = useState(null)

  socket.on('get location', (location) => {
    console.log('send location emited', location)
    setLocation(location)
  });

  useEffect(() => {
    console.log(location, ' location');
    if (location) {
      checkLocation(location)
    }
  }, [location])

  useEffect(() => {
    socket.on('get location', (location) => {
      console.log('send location emited', location)
      setLocation(location)
    });
    return () => socket.disconnect();
  }, [])

  const checkLocation = (location) => {

    // Living room
    // Lat: 32.080977305224984
    // Lng: 34.810573821163814

    // Lat: 32.080999578065054
    // Lng: 34.81048174788808

    // Lat: 32.08101910343775
    // Lng: 34.81061740960387

    // Lat: 32.08102595498077
    // Lng: 34.81054743074069

    // Kitchen
    // Lat: 32.08102596710672
    // Lng: 34.81054694602422

    // Lat: 32.080980279608326
    // Lng: 34.810636428252025

    // Lat: 32.080977390139516
    // Lng: 34.81071996021636

    // Lat: 32.08099948363042
    // Lng: 34.81073278566852


    let lat = location.lat;
    let lng = location.lng;
    // mapping rooms
    // if (lat > 32.08101910343775  && lat < 32.080999578065054 &&
    //   lng > 34.81054743074069 && lng < 34.81061740960387) {
    //     setRoom('Living Room')
    //   } 

    // if (lat > 32.080977390139516   && lat < 32.08102596710672 &&
    //     lng > 34.81054694602422  && lng < 34.81073278566852) {
    //       setRoom('Kitchen Room')
    // } 

    // barrier
    // Lat: 32.080985001695154
    // Lng: 34.81063506305676
    // mapping barrier
    if (lat > 32.080985001695154 && lng < 34.81063506305676 ) {
      setRoom('Living Room')
    }else if(lat < 32.080985001695154 && lng > 34.81063506305676){
      setRoom('Kitchen Room')
    }
    else {
      setRoom(null)
    }

  }



  return (
    <div>
      <p className="headline">Hellow World Dashboard :</p>
      {location && <div>
        <span className="bold">location.lat: <span>{location.lat}</span></span>
        <span className="bold">location.lng: <span> {location.lng}</span></span>
      </div>
      }
      <p>Area: </p>
      <p>Room:{room && <span>{room}</span>} </p>
      <div className='area'>
        <div className={room === "Living Room" ? "living-room on" : "living-room"}>
          Living room
          <img className="logo_symbol" src={logo_symbol} alt="" />
        </div>
        <div className={room === "Kitchen Room" ? "kitchen-room on" : "kitchen-room"}>
          Kitchen room
          <img className="logo_symbol" src={logo_symbol} alt="" />
        </div>
      </div>
    </div>
  );
}
