import React, { useContext, useEffect, useState } from 'react';
import { SocketContext } from '../../context/socket';


export default function Client() {
  const socket = useContext(SocketContext);
  const [watchID, setWatchID] = useState(null)
  const [currInterval, setCurrInterval] = useState(null)
  const [location, setLocation] = useState(null)
  const [rtt, setrtt] = useState(null)
  // const [isAutoLocation, seIsAutoLocation] = useState(null)
  const locationInterval = 10000;

  useEffect(() => {
    // const interval = setInterval(() => {
    //   console.log('This will run every second!');
    //   getLocation()
    // }, locationInterval);
    // setCurrInterval(interval);
    // return () => clearInterval(interval);
  }, []);


  useEffect(() => {
    // console.log('currInterval',currInterval)
  }, [currInterval]);

  useEffect(() => {
    if (location === null) {
      getLocation();
    }
  }, [location]);

  useEffect(() => {
    console.log('emit location', location)
    socket.emit('send location', location);
  }, [location]);


  const toggleInterval = async () => {
    console.log('currInterval', currInterval)
    if (watchID) {
      navigator.geolocation.clearWatch(watchID)
    } else {
      getLocation()
      // const interval = setInterval(() => {
      //   console.log('This will run every second! 2');
       
      // }, locationInterval);
      // setCurrInterval(interval);
      return () => navigator.geolocation.clearWatch(watchID);
    }
  }

  const getLocation = async () => {
    // var options = {
    //   enableHighAccuracy: true,
    // };
    // navigator.geolocation.getCurrentPosition((loc, error, options) => {
    //   // console.log("Latitude is :", location.coords.latitude);
    //   // console.log("Longitude is :", location.coords.longitude);
    //   }
    // );
    // var rtt = navigator.connection.rtt;
    // console.log('rtt ',rtt)
    // 
    let watchId = await navigator.geolocation.watchPosition(
      displayPosition,
      displayError,
      { enableHighAccuracy: true }
    );
    // console.log('watchId', watchId)
    // await navigator.geolocation.getCurrentPosition(
    //   displayPosition,
    //   displayError,
    //   { enableHighAccuracy: true }
    // );

    function displayPosition(loc) {
      // console.log('loc', loc)
      setLocation({
        lat: loc.coords.latitude,
        lng: loc.coords.longitude
      });
    }

    function displayError(error) {
      var errors = {
        1: 'Permission denied',
        2: 'Position unavailable',
        3: 'Request timeout'
      };
      alert("Error: " + errors[error.code]);
    }

  }

  return (
    <div>
      <p className="headline">Hellow World Client</p>
      <div className="actions">
        <button className="button" onClick={toggleInterval}>Auto Update Location {currInterval ? ' on' : 'off'}</button>
        <button className="button" onClick={getLocation}>Menual Update Location</button>
      </div>
      {location && <div>
        <p><span className="bold">Lat: </span> <span>{location.lat}  </span></p>
        <p><span className="bold">Lng: </span> <span>{location.lng}  </span></p>
      </div>}
      {rtt && <span>{rtt}</span>}
    </div>
  );
}